.button {
	display: block;
}

.modalInput {
	display: flex;
}

.modalBox {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 400px;
	background-color: #fff;
	border: 2px solid #000;
	box-shadow: 24px;
	padding: 4px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-content: center;
	align-items: center;
}
