.canvas {
  /* border: 1px solid #000; */
  /* width: 1800px; */
  /* height: 1800px; */
}

.root {
  /* min-height: 100vh; */
  display: flex;
  flex-flow: column wrap;
  background-color: #c4c4c4;
  justify-content: center;
  align-items: center;
  margin: 30px 50px;
  padding: 0px 30px 50px 30px;
}

.mintingButton {
  margin-top: 10px;
  margin-left: 30px;
}

.divider {
  width: 100%;
  height: 1px;
  background-color: #000;
  margin-bottom: 25px;
}

.header {
  display: flex;
  justify-content: space-evenly;
	align-items: center;
  width: 100%;
  /* height: 121px; */
	padding: 8px 12px;
  /* margin-bottom: 30px; */
}
@media screen and (max-width:1268px) {
	.header {
		flex-direction: column;
	}
	
}

.headerButton {
  flex-direction: column;
  justify-content: center;
}

.headerLeft {
  width: 20%;
  display: flex;
  justify-content: center;
  /* align-items: end; */
}

.date {
  font-size: 4rem;
  font-weight: 700;
  margin: 0;
  vertical-align: baseline;
}

.headerCenter {
  width: 60%;
  display: flex;
  justify-content: center;
}

.title {
  font-family: "DM Serif Text";
  font-size: 7rem;
  font-weight: 400;
  margin: 0;
  vertical-align: baseline;
  /* height: fit-content; */
}

.headerRight {
  width: 20%;
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  align-items: center;
}

.headerButtons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.buttons {
  /* display: b; */
  font-size: 32px;
  font-weight: 700;
	cursor:pointer;
  /* margin-right: 10px; */
}

.drawer {
  width: 500px;
  height: 100%;
  /* background-color: rgba(0, 0, 0, 0); */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
